@import url(//db.onlinewebfonts.com/c/a4e40941cd04772dbe49aa5790ad5905?family=OPTICalculator);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }

.calculator-container {
  border: 2px solid #47476b;
  padding: 5px;
  width: 320px;
  position: relative;
  display: inline-block;
  box-shadow: 5px 5px; }
  .calculator-container #op-display {
    background-color: black;
    width: 320px;
    min-height: 20px;
    line-height: 20px;
    color: yellow;
    text-align: right;
    font-size: 16px;
    font-family: OPTICalculator; }
  .calculator-container #display {
    background-color: black;
    width: 320px;
    line-height: 30px;
    color: white;
    text-align: right;
    font-size: 25px;
    font-family: OPTICalculator; }
  .calculator-container .cal-button {
    position: relative;
    float: left;
    width: 80px;
    height: 65px;
    text-align: center;
    text-justify: center;
    border: 1px solid black;
    padding-top: 25px;
    border-radius: 5px;
    box-sizing: border-box;
    cursor: pointer; }
  .calculator-container #clear, .calculator-container #zero {
    width: 160px; }

